





































































































































import { Component, Prop, Vue } from "vue-property-decorator";
import { computed, ref } from "@vue/composition-api";
import AppStatus from "../AppStatus.vue";
import AppImage from "@/components/AppImage.vue";
import { Fine } from "@/models/fine.entity";
import { Car } from "@/models/car.entity";
import { DriverEntity } from "@/models/driver.entity";
@Component({
  components: { AppStatus, AppImage },
  setup(props, { emit }) {
    const payCard = () => {
      emit("payCard");
    };
    const payBalance = () => {
      emit("payBalance");
    };
    return { payCard, payBalance };
  },
})
export default class DriverListItem extends Vue {
  @Prop(Object) item: Fine;
  get responsiveHeader() {
    return "col-sm-6 col-md-4 col-xl-2";
  }
  get responsiveContent() {
    return "col-12 col-xl-4";
  }
  get statusText() {
    if (this.item.status === "paid") {
      return "Оплачен";
    } else if (this.item.status === "pending") {
      return "В ожидании";
    } else {
      return "Не оплачен";
    }
  }
  get statusColor() {
    if (this.item.status === "paid") {
      return "green";
    } else if (this.item.status === "pending") {
      return "orange";
    } else {
      return "red";
    }
  }
  get car() {
    return this.item?.car || ({} as Car);
  }
  get driver() {
    return this.item?.driver || ({} as DriverEntity);
  }
  get currency() {
    return this.$store.getters.currency;
  }
}
